import React from "react";
import styles from "./phonebook.module.css";

export default function Filter({ handleInput, value }) {
  return (
    <label>
      Фільтрувати по імені{" "}
      <input
        className={styles.input_field}
        type="text"
        value={value}
        onChange={handleInput}
        name="filterW"
      />
    </label>
  );
}
