import React from "react";
import styles from "./feedback.module.css";

function Feedback({ props, total, percent }) {
  const { good, bad, neutral } = props;
  return (
    <>
      <p>Круть: {good}</p>
      <p>Так сє: {neutral}</p>
      <p>Жуть: {bad}</p>
      <p className={styles.black_yellow}>Разом: {total}</p>
      <p className={styles.black_yellow}>Відсоток позитивних: {percent}%</p>
    </>
  );
}
export default Feedback;
