import React, { Component } from "react";
import Feedback from "./feedbacks/Feedback";
import FBmanage from "./feedbacks/FBmanage";
import Section from "./feedbacks/Section";

export default class App1 extends Component {
  static defaultProps = {};
  static propTypes = {};

  state = {
    good: 0,
    bad: 0,
    neutral: 0,
  };

  countTotalFeedback = () => {
    return this.state.good + this.state.bad + this.state.neutral;
  };

  countPositiveFeedbackPercentage = () => {
    return ((this.state.good * 100) / this.countTotalFeedback()).toFixed(2);
  };

  increaseFeeds = (event) => {
    const { name } = event.target;
    this.setState((prevState) => {
      return { [name]: prevState[name] + 1 };
    });
  };

  render() {
    return (
      <>
        <Section
          title="Залиште відгук, будь ласка"
          children={<FBmanage onIncreaseFeeds={this.increaseFeeds} />}
          classNameProp="feedback_form"
        />
        {this.countTotalFeedback() > 0 && (
          <Section
            title="Статистика"
            children={
              <Feedback
                props={this.state}
                total={this.countTotalFeedback()}
                percent={this.countPositiveFeedbackPercentage()}
              />
            }
            classNameProp="statistics_form"
          />
        )}
      </>
    );
  }
}
