import React from "react";
import styles from "./feedback.module.css";

function FBmanage({ onIncreaseFeeds }) {
  return (
    <div>
      <button
        className={styles.feedButton}
        type="button"
        name="good"
        onClick={onIncreaseFeeds}
      >
        Круть
      </button>
      <button
        className={styles.feedButton}
        type="button"
        name="neutral"
        onClick={onIncreaseFeeds}
      >
        Так сє
      </button>
      <button
        className={styles.feedButton}
        type="button"
        name="bad"
        onClick={onIncreaseFeeds}
      >
        Жуть
      </button>
    </div>
  );
}
export default FBmanage;
